import {_$} from '../utils';

function now() {
    return new Date();
}

function days(days) {
    const now = new Date();
    now.setDate(now.getDate() + days);
    return now;
}

function months(months) {
    const now = new Date();
    now.setMonth(now.getMonth() + months);
    return now;
}

export const today = {
    label() { return _$(''); },
    value() { return now(); },
};

export const yesterday = {
    label() { return _$(''); },
    value() { return days(-1); },
};

export const tomorrow = {
    label() { return _$(''); },
    value() { return days(1); }
};

export const afterOneWeek = {
    label() { return _$(''); },
    value() { return days(7); },
};

export const afterOneMonth = {
    label() { return _$(''); },
    value() { return months(1); } 
};

export const afterThreeMonths = {
    label() { return _$(''); },
    value() { return months(3); },
};

export const beforeOneWeek = {
    label() { return _$(''); },
    value() { return days(-7); },
};

export const beforeOneMonth = {
    label() { return _$(''); },
    value() { return months(-1); } 
};

export const beforeThreeMonths = {
    label() { return _$(''); },
    value() { return months(-3); },
};

export const lastWeek = {
    label() { return _$(''); },
    value() { return [days(-7), now()]; },
};

export const nextWeek = {
    label() { return _$(''); },
    value() { return [now(), days(7)]; },
};

export const lastMonth = {
    label() { return _$(''); },
    value() { return [months(-1), now()]; }
};

export const nextMonth = {
    label() { return _$(''); },
    value() { return [now(), months(1)]; }
};

export const lastThreeMonths = {
    label() { return _$(''); },
    value() { return [months(-3), now()]; }
};

export const nextThreeMonths = {
    label() { return _$(''); },
    value() { return [now(), months(3)]; }
};
